body {
  margin: 0;
  position: relative;
  padding-top: 220px;
}

.container {
  width: 900px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width:$tablet-width) {
    
  }
}

.page__body {
  background-color: black;
  color: white;
}

.page-header {
  border-bottom: 1px solid #ffffff;
  padding: 20px 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: black;
 
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  img {
    width: 150px;
    height: 150px;
  }

  h1 {
    font-size: 23px;
    margin: 0;
    font-weight: bold;
    text-transform: uppercase;
  }
}

.page-main {
  .photo-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    &:nth-child(even) {
      flex-direction: row-reverse;
    }
  }

  img {
    max-width: 500px;
    margin-right: 30px;
    max-height: 400px;
  }

  .photo-text {
    font-size: 20px;
    font-weight: bold;
    line-height: normal;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    max-width: 370px;
  }
}